.wrapper {
  display: flex;
  justify-content: center;
  padding-top: 120px;
}

.body {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.link {
  color: #4c4cff;
  text-decoration: none;
}

.controls {
  width: 450px;
  display: flex;
  justify-content: space-around;
  margin-top: 50px;
}

.controls button {
  width: 180px;
}

.controls button button {
  width: 180px;
}
